import store from './store'
import router from './router'
import Cookies from 'js-cookie'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'

NProgress.configure({ showSpinner: false })

const isLogin = Cookies.get('auth')

router.beforeEach((to, from, next) => {
  NProgress.start()
  document.title = to.meta.tip || '彼益网络 - 后台管理'
  if (!isLogin && to.path !== '/login') {
    next('/login') // 重定向到登录页
    NProgress.done()
    return
  }
  if (!isLogin && to.path === '/login') {
    next()
    return
  }
  if (isLogin && to.path === '/login') {
    next('/')
    NProgress.done()
    return
  }
  if (isLogin && store.state.roles.length === 0) {
    store.dispatch('fetchUserInfo').then(roles => { // 拉取用户权限
      store.dispatch('createdRoutes', { roles }).then(() => { // 根据权限生成可访问的路由表
        router.addRoutes(store.state.addRouters) // 动态添加可访问路由表
        next({ ...to, replace: true }) // hack方法 确保addRoutes已完成
      })
    }).catch(err => {
      ELEMENT.Message.error(err || 'Verification failed, please login again')
      next('/')
    })
  } else {
    next()
  }
})

router.afterEach(() => {
  NProgress.done() 
})



