<template>
  <el-menu class="navbar" mode="horizontal">
    <hamburger class="hamburger-container" :toggleClick="toggleSideBar" :isActive="sidebar.opened"></hamburger>

    <breadcrumb class="breadcrumb-container"></breadcrumb>

    <div class="right-menu">

      <el-tooltip v-if="device === 'desktop'" effect="dark" content="全屏" placement="bottom">
        <screenfull class="screenfull right-menu-item"></screenfull>
      </el-tooltip>

      <el-tooltip effect="dark" content="修改密码" placement="bottom">
        <span @click="resetPassword">
          <svg-icon class="exit" icon-class="password"></svg-icon>
        </span>
      </el-tooltip>

      <el-tooltip effect="dark" content="退出" placement="bottom">
        <span @click="exit">
          <svg-icon class="exit" icon-class="exit"></svg-icon>
        </span>
      </el-tooltip>
    </div>
  </el-menu>
</template>

<script>
import Breadcrumb from "@/components/Breadcrumb"
import Hamburger from "@/components/Hamburger"
import Screenfull from "@/components/Screenfull"
import { fetchData } from '@/api'
import Cookies from 'js-cookie'
export default {
  components: {
    Breadcrumb,
    Hamburger,
    Screenfull
  },
  computed: {
    sidebar: function() {
      return this.$store.state.sidebar
    },
    device: function() {
      return this.$store.state.device
    }
  },
  methods: {
    // 切换侧边栏
    toggleSideBar() {
      this.$store.dispatch('toggleSideBar')
    },
    // 退出
    exit() {
      fetchData('userLogout').then(user => {
        if (user) window.location.href = `${window.location.origin}/login`
      })
    },
    // 修改密码
    resetPassword() {
      const auth = Cookies.get('auth')
      const temp = auth.split('|')
      if (auth) {
        ELEMENT.MessageBox.prompt('请输入新密码(6~12位数字或字母组合)', '修改密码', {
          confirmButtonText: '确定',
          cancelButtonText: '取消'
        }).then(({ value }) => {
          if (value && value.length > 5) {
            fetchData('resetPassword', {
              id: temp[0],
              old_psw: Cookies.get('password'),
              new_psw: value
            }).then(data => {
              if (data) {
                Cookies.remove('auth')
                window.location.href = `${window.location.origin}/login`
              }
            })
          } else {
            ELEMENT.Message.error('密码不符合要求')
          }
        }).catch(Err => console.log(Err))
      } else {
        ELEMENT.Message.error('账号异常、请重新登录')
      }
    }
  }
};
</script>

<style lang="less" scoped>
.navbar {
  height: 50px;
  line-height: 50px;
  border-radius: 0px !important;
  .hamburger-container {
    line-height: 58px;
    height: 50px;
    float: left;
    padding: 0 10px;
  }
  .breadcrumb-container {
    float: left;
  }
  .right-menu {
    float: right;
    height: 100%;
    padding-right: 20px;
    &:focus {
      outline: none;
    }
    .right-menu-item {
      display: inline-block;
      margin: 0 8px;
      vertical-align: middle;
    }
    .screenfull {
      height: 20px;
    }
    .exit {
      display: inline-block;
      cursor: pointer;
      fill: #5a5e66;
      width: 20px;
      height: 20px;
      vertical-align: middle;
      margin: 0 10px;
    }
  }
}
</style>




