import { fetchData } from '@/api'
import { asyncRouter } from '@/router/config'

function hasPermission(roles, route) {
  if (route.meta && route.meta.roles) {
    return roles.some(role => route.meta.roles.indexOf(role) >= 0)
  } else {
    return true
  }
}

function filterAsyncRouter(asyncRouterMap, roles) {
  const accessedRouters = asyncRouterMap.filter(route => {
    if (hasPermission(roles, route)) {
      if (route.children && route.children.length) {
        route.children = filterAsyncRouter(route.children, roles)
      }
      return true
    }
    return false
  })
  return accessedRouters
}

export default {
  // 获取用户信息
  fetchUserInfo({ commit }) {
    return new Promise((resolve, reject) => {
      const temp = []
      fetchData('fetchNowModule').then(data => {
        if (data && data.length) { 
          data.map(v => temp.push(v.module_name))
          commit('SET_ROLES', temp)
        } else {
          temp.push('default')
        }
        resolve(temp)
      }).catch(error => {
        reject(error)
      })
    })
  },
  // 添加访问视图
  addVisitedViews({ commit }, view) {
    commit('ADD_VISITED_VIEWS', view)
  },
  // 移除访问视图
  delVisitedViews({ commit, state }, view) {
    return new Promise((resolve) => {
      commit('DEL_VISITED_VIEWS', view)
      resolve([...state.visitedViews])
    })
  },
  // 移除其它视图
  delOthersViews({ commit, state }, view) {
    return new Promise((resolve) => {
      commit('DEL_OTHERS_VIEWS', view)
      resolve([...state.visitedViews])
    })
  },
  // 移除全部视图
  delAllViews({ commit, state }) {
    return new Promise((resolve) => {
      commit('DEL_ALL_VIEWS')
      resolve([...state.visitedViews])
    })
  },
  // 创建可访问路由
  createdRoutes({ commit, state }, data) {
    return new Promise(resolve => {
      const { roles } = data
      let accessedRouters
      accessedRouters = filterAsyncRouter(asyncRouter, roles)
      commit('SET_ROUTERS', accessedRouters)
      resolve()
    })
  },
  // 切换侧边栏
  toggleSideBar({ commit }) {
    commit('TOGGLE_SIDEBAR')
  },
  // 关闭侧边栏
  closeSideBar({ commit }, { withoutAnimation }) {
    commit('CLOSE_SIDEBAR', withoutAnimation)
  },
  // 切换设备
  toggleDevice({ commit }, device) {
    commit('TOGGLE_DEVICE', device)
  },
  // 更新搜索标签
  updateTagList({ commit }, value) {
    commit('UPDATE_TAG_LIST', value)
  }
}


