<template>
  <section class="app-main" style="min-height: 100%;height: 100%">
    <transition name="fade" mode="out-in">
      <keep-alive :exclude="cachedViews">
        <router-view></router-view>
      </keep-alive>
    </transition>
  </section>
</template>

<script>
export default {
  name: 'AppMain',
  computed: {
    cachedViews() {
      return this.$store.state.cachedViews
    }
  }
}
</script>



