// import Vue from 'vue'
import Vuex from 'vuex'
import actions from './actions'
import mutations from './mutations'
import { constantRouter } from '@/router/config'

const state = {
  sidebar: {
    opened: true,
    withoutAnimation: false
  },
  device: 'desktop',
  roles: [],   // 用户可访问路由
  addRouters: [],  // 动态添加的路由
  visitedViews: [],  // 访问视图
  cachedViews: ['admin'],  // 不需要缓存的视图
  routers: constantRouter, // 路由对象 
  tagList: [] // 搜索的标签列表
}

Vue.use(Vuex)

const store = new Vuex.Store({
  state,
  actions,
  mutations
})

export default store



