import Main from '@/views/main/Main.vue'

const _import = file => () => import('@/views/' + file + '.vue')

export const constantRouter = [
  {
    path: '',
    component: Main,
    redirect: 'home',
    children: [{
      path: 'home',
      component: _import('home/Home'),
      name: 'home',
      meta: { 
        title: '首页', 
        icon: 'international'
      }
    }]
  },
  {
    path: '/login',
    name: 'login', 
    component: _import('login/Login'), 
    hidden: true,
    meta: {
      tip: 'Login - 登录'
    }
  }
]

export const asyncRouter = [
  {
    path: '/default', // 默认页面
    name: 'default',
    component: Main,
    redirect: '/default/index',
    meta: {roles: 'default'},
    children: [
      {
        path: 'index',
        name: 'defaultIndex',
        component: _import('default/Index'),
        meta: {title: '默认目录', icon: 'people', roles: 'default'}
      }
    ]
  },
  {
    path: '/access', // 权限管理
    name: 'access',
    component: Main,
    redirect: '/access/admin',
    meta: {title: '权限管理', icon: 'lock', roles: 'admin'},
    children: [
      {
        path: 'admin',
        name: 'accessAdmin',
        component: _import('access/Admin'),
        meta: {title: '管理员', roles: 'admin'}
      },
      {
        path: 'group',
        name: 'accessGroup',
        component: _import('access/Group'),
        meta: {title: '权限组', roles: 'admin'}
      }
    ]
  },
  {
    path: '/backstage', // 后台管理
    name: 'backstage',
    component: Main,
    redirect: '/backstage/config',
    meta: {title: '后台管理', icon: 'dashboard', roles: 'admin'},
    children: [
      {
        path: 'config',
        name: 'backstageConfig',
        component: _import('backstage/Config'),
        meta: {title: '全局配置', roles: 'admin'}
      },
      {
        path: 'log',
        name: 'backstageLog',
        component: _import('backstage/Log'),
        meta: {title: '管理日志', roles: 'adminLog'}
      }
    ]
  },
  {
    path: '/member', // 会员管理
    name: 'member',
    component: Main,
    redirect: '/member/user',
    meta: {title: '会员管理', icon: 'peoples', roles: 'user'},
    children: [
      {
        path: 'user',
        name: 'memberUser',
        component: _import('member/User'),
        meta: {title: '用户管理', roles: 'user'}
      },
      {
        path: 'online',
        name: 'memberOnline',
        component: _import('member/Online'),
        meta: {title: '在线用户', roles: 'user'}
      }
    ]
  },
  {
    path: '/forward', // 充值提现
    name: 'forward',
    component: Main,
    redirect: '/forward/pay',
    meta: {title: '充值提现', icon: 'shoppingCard', roles: 'order'},
    children: [
      {
        path: 'pay',
        name: 'forwardPay',
        component: _import('forward/Pay'),
        meta: {title: '充值订单', roles: 'order'}
      },
      {
        path: 'put',
        name: 'forwardPut',
        component: _import('forward/Put'),
        meta: {title: '提现订单', roles: 'order'}
      }
    ]
  },
  {
    path: '/futures', // 期货管理
    name: 'futures',
    component: Main,
    redirect: '/futures/position',
    meta: {title: '期货管理', icon: 'example', roles: 'history'},
    children: [
      {
        path: 'position',
        name: 'futuresPosition',
        component: _import('futures/Position'),
        meta: {title: '持仓管理', roles: 'history'}
      },
      {
        path: 'order',
        name: 'futuresOrder',
        component: _import('futures/Order'),
        meta: {title: '订单管理', roles: 'history'}
      },
      {
        path: 'history',
        name: 'futuresHistory',
        component: _import('futures/History'),
        meta: {title: '历史查询', roles: 'history'}
      }
    ]
  },
  {
    path: '/bankroll', // 资金管理
    name: 'bankroll',
    component: Main,
    redirect: '/bankroll/assets',
    meta: {title: '资金管理', icon: 'theme', roles: 'wallet'},
    children: [
      {
        path: 'assets',
        name: 'bankrollAssets',
        component: _import('bankroll/Assets'),
        meta: {title: '用户资产', roles: 'wallet'}
      },
      {
        path: 'stream',
        name: 'bankrollStream',
        component: _import('bankroll/Stream'),
        meta: {title: '资产流水', roles: 'walletLog'}
      }
    ]
  },
  // 404 页面一定要最后加载、否则后面的所有页面都会被拦截到404
  {
    path: '*', 
    name: 'error',
    component: _import('error/Error'), 
    hidden: true,
    meta: {
      tip: '404-页面不存在'
    }
  }
]



